<template>

  <div class="cms-list-layout">
    <cms-breadcrumb to="/shopMember/list" type='list' >
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
     <el-form ref="memberTab" :rules="rules" :model="formData" label-width="210px"  >
        <el-form-item label="用户名"  >
          <!-- <img src=""> resourceData-->
          <span>{{formData.username}}</span>
        </el-form-item>
        <el-form-item label="账户余额" >
          <span></span>
        </el-form-item>
        <el-form-item label="积分"  >
          <span></span>
        </el-form-item>
        <el-form-item label="会员成长值/会员等级"  >
          <span></span>/<span>{{formData.memberLevel}}</span>
        </el-form-item>
        <el-form-item label="邮箱" prop="email" :error='errorMessage.email'>
          <el-input v-model="formData.email" ></el-input>
        </el-form-item>
        <el-form-item label="手机"  >
          <el-input v-model="formData.phone"  ></el-input>
        </el-form-item>
        <el-form-item label="密码"  >
          <el-input v-model="formData.password"></el-input>
          <div class="detail">留空则不修改</div>
        </el-form-item>
        <el-form-item label="姓名" >
          <el-input v-model="formData.username" ></el-input>
        </el-form-item>
        <el-form-item label="性别"  >
          <el-radio-group v-model="gender">
            <el-radio :label="1">男</el-radio>
            <el-radio :label="2">女</el-radio>
            <el-radio :label="3">保密</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="生日" >
          <el-date-picker
            v-model="formData.birthday"
            type="date"
            size="small"
            placeholder="年/月/日">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="是否启用" >
          <cms-switch  v-model="formData.enabled" ></cms-switch>
          <div class="detail">设置为禁用后，该会员将无法登录商城</div>
        </el-form-item>
      </el-form>
      <h3 class="cms-segmentation">账户绑定</h3>
      <el-form  ref="form"  :model="formData" label-width="210px">
        <el-form-item label="QQ"  >
          <span>未绑定</span>
          <span v-if="formData.memberThirdList === 0">已绑定 <el-button>解绑</el-button></span>
        </el-form-item>
        <el-form-item label="微信"  >
          <span>未绑定</span>
          <span v-if="formData.memberThirdList === 0">已绑定 <el-button>解绑</el-button></span>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
        <cms-button @click="update" v-color >保存</cms-button>
        <!-- <cms-button type='rest' @click="rest">重置</cms-button> -->
      </div>
  </div>
</template>

<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import { memberGet, memberUpdate } from './../../../../api/users/user.js'
var defaultData = {
}
export default {
  mixins: [form],
  data() {
    return {
      checked1: false,
      checked2: false,
      gender: '',
      interestsDialog: false,
      formData: Object.assign({}, defaultData),
      queryData: {
        member: {
          email: '',
          enabled: '',
          id: ''
        },
        memberExt: {
          gender: ''
        },
        memberThirdList: []
      },
      rules: {
        email: [rules.required('此项为必填'), rules.email()]
        // levelExp: [rules.required('此项为必填')]
      }
    }
  },
  methods: {
    getFormData(id) { // 通过id获取表单信息
      memberGet({ id: id }).then(res => {
        this.formData = Object.assign(defaultData, res.data)
      })
    },
    update() {
      this.queryData.member = { email: this.formData.email, enabled: this.formData.enabled, id: this.id }
      this.queryData.memberExt.gender = this.gender
      this.queryData.memberThirdList.push({ memberId: this.formData.memberId, thirdId: this.id, thirdTypeCode: '8080', username: this.formData.username })
      if (this.formData.password !== '') {
        this.queryData.member.password = this.formData.password
      }

      this.updateForm(memberUpdate, this.queryData, '/shopMember/list', 'list')
    },
    changeData() { // 通过ref来触发
      this.getFormData(this.id)
    }
  },
  created() {
    this.getFormData(this.id)
  }
}
</script>
<style lang="scss" scoped>
.el-form-item .detail{
  color: #999999;
  font-size: 13px;
}
</style>




