<template>
  <div class="cms-list-layout">
    <cms-breadcrumb to="/shopMember/list" type='list' >
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <cms-tabs>
      <cms-tab to="/shopMember/edit">基本信息</cms-tab>
      <cms-tab to="/shopMember/account">账户明细</cms-tab>
      <cms-tab to="/shopMember/grow">成长值明细</cms-tab>
      <cms-tab to="/shopMember/integral">积分明细</cms-tab>
    </cms-tabs>
    <cms-prompt collapse='true'>
      <li>显示商城会员账户金额所有的交易明细</li>
      <li>可对交易记录进行删除操作（谨慎操作）</li>
    </cms-prompt>
    <section class="cms-list-query">
      <div class="query-group">
        <label>类型</label>
        <el-select v-model="tableData.changeType">
          <el-option label="全部" value=""></el-option>
          <el-option label="收入（充值）" :value="'1'"></el-option>
          <el-option label="收入（退款）" :value="'2'"></el-option>
          <el-option label="支出（提现）" :value="'3'"></el-option>
          <el-option label="支出（交易）" :value="'4'"></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <label>状态</label>
        <el-select v-model="tableData.state">
          <el-option label="全部" value=""></el-option>
          <el-option label="成功" :value="'1'"></el-option>
          <el-option label="失败" :value="'2'"></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <label>交易方式</label>
        <el-select v-model="value">
          <el-option label="全部" value=""></el-option>
          <el-option label="支付宝" :value="'1'"></el-option>
          <el-option label="微信" :value="'2'"></el-option>
          <el-option label="银联" :value="'3'"></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <label>交易流水号</label>
        <el-input  @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>业务号</label>
        <el-input  @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>生成时间</label>
        <el-time-picker is-range range-separator="至" start-placeholder="开始时间" end-placeholder="结束时间" placeholder="选择时间范围" @keyup.native.enter="query">
        </el-time-picker>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">导出明细</cms-button>
      </div>
    </section>
    <section class="cms-batch">
      <cms-button icon="error" type="error" size="small" @click="deleteTableDatas(ids)">批量删除</cms-button>
    </section>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' ref='dataTable'>
      <el-table-column type="selection" width="46" fixed='left'></el-table-column>
      <el-table-column prop="id" label="id" width="60"></el-table-column>
      <el-table-column prop="username" label="会员账户" min-width="200"></el-table-column>
      <el-table-column prop="tradeTypeCode" label="类型" min-width="150"></el-table-column>
      <el-table-column prop="businessNo" label="业务号" min-width="150"></el-table-column>
      <el-table-column prop="tradeNo" label="交易流水号" min-width="150"></el-table-column>
      <el-table-column prop="tradeWayCode" label="交易方式" min-width="150"></el-table-column>
      <el-table-column prop="tradeMoney" label="金额" min-width="150"></el-table-column>
      <el-table-column prop="balance" label="余额" min-width="150"></el-table-column>
      <el-table-column prop="state" label="状态" min-width="110">
        <div slot-scope="scope">
          <span v-if="scope.row.status === '1'">待支付</span>
          <span v-else-if="scope.row.status === '2'">未完成</span>
          <span v-else-if="scope.row.status === '3'">已完成</span>
          <span v-else-if="scope.row.status === '4'">已取消</span>
        </div>
      </el-table-column>
      <el-table-column prop="tradingTime" label="交易时间" min-width="150"></el-table-column>
      <el-table-column label="操作选项" width='175' fixed="right" align="center">
        <div slot-scope="scope">
          <cms-button icon='error' size='small' type='info' @click="deleteTableData">删除</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>

<script>
import { memberAccountDetails, memberAccountDetailsDelete } from './../../../../api/users/user.js'
import list from '@/mixins/list'
var defaultData = {
  page: 1,
  size: 15
}
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData),
      value: ''
    }
  },
  methods: {
    deleteData(id) { // 包装一层删除的操作
      this.deleteTableData(memberAccountDetailsDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas(ids) {
      this.deleteTableDatas(memberAccountDetailsDelete, ids) // 这个才是真实的删除方法
    },
    changeData() { // 通过ref来触发
      this.getTableData(memberAccountDetails, this.queryParams)
    }
  },
  created() {
    this.getTableData(memberAccountDetails, this.queryParams)
  }
}
</script>
<style>

</style>



