<template>
  <div class="cms-list-layout" style="padding-top:6px">
    <el-tabs v-model="activeName" v-if="status" ref='memberTab'>
      <el-tab-pane label="基本信息" name="first">
        <sale ref='edit' @change="eventChange"></sale>
      </el-tab-pane>
      <el-tab-pane label="账户明细" name="second" lazy>
        <audit ref="account"  @change="eventChange"></audit>
      </el-tab-pane>
      <el-tab-pane label="成长值明细" name="third" lazy>
        <shelves ref="grow" @change="eventChange"></shelves>
      </el-tab-pane>
      <el-tab-pane label="积分明细" name="fourth" lazy>
        <recycle ref="integral"  @change="eventChange"></recycle>
      </el-tab-pane>
    </el-tabs>

  </div>
</template>

<script>
import edit from './edit'
import account from './account'
import grow from './grow'
import integral from './integral'
export default {
  name: 'memberEdit',
  components: {
    edit,
    account,
    grow,
    integral
  },
  data() {
    return {
      status: true,
      activeName: 'first'
    }
  },
  methods: {
    eventChange(ev) {
      // 出售中列表状态更新
      if (ev === 'edit') {
        if (this.$refs['edit']) {
          this.$refs['edit'].changeData() // 调用列表更新
        }
      }
      if (ev === 'account') {
        if (this.$refs['account']) {
          this.$refs['account'].changeData() // 调用列表更新
        }
      }
      if (ev === 'grow') {
        if (this.$refs['grow']) {
          this.$refs['grow'].changeData() // 调用列表更新
        }
      }
      if (ev === 'integral') {
        if (this.$refs['integral']) {
          this.$refs['integral'].changeData() // 调用列表更新
        }
      }
    }

  },
  activated() {
    if (this.$route.query.type === 'edit') {
      this.status = false
      setTimeout(() => {
        this.status = true
      }, 1)
    }
  }
}
</script>

<style>
</style>



